/**
 * 
 */

class Validate {
  constructor() {
    // 
    this._regular = {
      // 
      mobile: /^[1][3,4,5,7,8,9][0-9]{9}$/,
      // 
      email: /^(\w-*\.*)+@(\w-?)+(\.\w{2,})+$/,
      // 
      idcard: /^\d{6}(19|20)?\d{2}(0[1-9]|1[012])(0[1-9]|[12]\d|3[01])\d{3}(\d|X)$/,
      // 
      isdecimal: /^[0-9]*([.]{1}[0-9]*){0,1}$/,
      // 
      alphanumeric: /^[A-Za-z0-9]+$/,
      // 
      letter: /^[A-Za-z]+$/,
      // 
      lowercaseletters: /^[a-z]+$/,
      // 
      format1: /^[A-Za-z,.!@$%^&*_+=;:?./]+$/,
      // 
      format2: /^[\u4e00-\u9fa5A-Za-z0-9,.!@$%^&*_+=;:?./]+$/,
      // 
      format3: /^[A-Za-z0-9,.!@$%^&*_+=;:?./]+$/,
      // 6
      password: /^[0-9A-Za-z]{6,}$/,
      //
      chinsesName: /^[\u4E00-\u9FA5\uf900-\ufa2ds]{2,20}$/,
    }
  }

  /**
   * 
   * @param {*} message 
   * @param {*} trigger 
   */
  verifyRequired = (message = '', trigger = 'change') => {
    return {
      required: true,
      message: message,
      trigger: trigger
    }
  }

  /**
   * 
   */
  verifyPhone = (rule, value, callback) => {
    // console.log(rule)
    if (value && !this._regular.mobile.test(value)) {
      callback(new Error(''))
    } else {
      callback()
    }
  }

  /**
   * 
   */
  verifyEmail = (rule, value, callback) => {
    if (value && !this._regular.email.test(value)) {
      callback(new Error(''))
    } else {
      callback()
    }
  }

  /**
   * 
   */
  verifyIdcard = (rule, value, callback) => {
    if (value && !this._regular.idcard.test(value)) {
      callback(new Error(''))
    } else {
      callback()
    }
  }

  /**
   * 
   */
  verifyIsdecimal = (rule, value, callback) => {
    if (value && !this._regular.isdecimal.test(value)) {
      callback(new Error(''))
    } else {
      callback()
    }
  }

  /**
   * 
   */
  verifyAlphanumeric = (rule, value, callback) => {
    if (value && !this._regular.alphanumeric.test(value)) {
      callback(new Error(''))
    } else {
      callback()
    }
  }

  /**
   * 
   */
  verifyLetter = (rule, value, callback) => {
    if (value && !this._regular.letter.test(value)) {
      callback(new Error(''))
    } else {
      callback()
    }
  }
  /**
   * 
   */
  verifyChinsesName = (rule, value, callback) => {
    if (value && !this._regular.chinsesName.test(value)) {
      callback(new Error(''))
    } else {
      callback()
    }
  }

  /**
   * 
   */
  verifyLowercaseletters = (rule, value, callback) => {
    if (value && !this._regular.lowercaseletters.test(value)) {
      callback(new Error(''))
    } else {
      callback()
    }
  }

  /**
   * 
   */
  verifyFormat1 = (rule, value, callback) => {
    if (value && !this._regular.format1.test(value)) {
      callback(new Error(''))
    } else {
      callback()
    }
  }

  /**
   * 
   */
  verifyFormat2 = (rule, value, callback) => {
    if (value && !this._regular.format2.test(value)) {
      callback(new Error(''))
    } else {
      callback()
    }
  }

  /**
   * 
   */
  verifyFormat3 = (rule, value, callback) => {
    if (value && !this._regular.format3.test(value)) {
      callback(new Error(''))
    } else {
      callback()
    }
  }
  //
  verifyPwd = (rule, value, callback) => {
    if (value && !this._regular.password.test(value)) {
      callback(new Error('6'))
    } else {
      callback()
    }
  }
}

/*
  

  import { validate } from '@/plugin/validate'
  rules:{
    username: [validate.verifyRequired('')],
    password: [validate.verifyRequired('')],
    phone: [
      { validator: validate.verifyPhone, trigger: 'change' }
    ],
    email: [
      validate.verifyRequired(''),
      { validator: validate.verifyEmail, trigger: 'change' }
    ]
  }
   
*/


let validate = new Validate()

export {
  validate
}