<template>
  <div>
    <div v-loading="loading"
      class="uploadFile">
      <div class="uploadFileForm">
        <el-form :rules="rules"
          ref="uploadFileForm"
          :inline="false"
          :model="uploadFileForm">
          <el-row>
            <el-col :span="12">
              <el-form-item prop="check_time"
                label="">
                <el-date-picker v-model="uploadFileForm.check_time"
                  type="date"
                  :picker-options="pickerDisabled"
                  placeholder=""></el-date-picker>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label=""
                prop="report_name">
                <el-input style="width:220px"
                  maxlength="50"
                  v-model="uploadFileForm.report_name"
                  placeholder=""
                  value-format="timestamp"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <!--  -->
          <br />
          <el-form-item style="width:100%"
            label
            prop="report_file_list">
            <div class="uploadBox">
              <uploadFile ref="uploadFiles"
                @getFiles="getFiles"
                :files="files"></uploadFile>
            </div>
          </el-form-item>
          <br />
          <el-form-item>
            <el-button type="primary"
              @click="tosubmit('uploadFileForm')"></el-button>
            <!-- <el-button></el-button> -->
          </el-form-item>
        </el-form>
        <p>
          <!--  -->
          <br />.pdf .docx .doc 
          <br />.jpg .jpeg .png .bmp 
          <br />
          3MB
        </p>
      </div>
    </div>
  </div>
</template>

<script>
import { validate } from "@/utils/validate";
import uploadFile from "@/components/uploadFile/index";
export default {
  name: "upload",
  components: {
    uploadFile
  },
  data() {
    return {
      loading: false,
      pickerDisabled: {
        disabledDate: time => {
          return time.getTime() > new Date().getTime();
        }
      },
      rules: {
        report_name: [validate.verifyRequired("")],
        check_time: [validate.verifyRequired("")],
        report_file_list: [validate.verifyRequired("")]
      },
      fileUrl: [],
      uploadFileForm: {
        check_time: "",
        report_name: "",
        report_file_list: []
      },
      uploadConfig: {
        type: ""
      },
      //
      files: []
    };
  },
  methods: {
    //
    uploadSuccess(fileUrl) {
      // console.log("");
      // console.log(fileUrl);
      this.fileUrl = fileUrl;
    },
    //
    tosubmit(formName) {
      //
      // console.log(this.uploadFileForm);
      this.$refs[formName].validate(valid => {
        if (valid) {
          let param = {
            ...this.uploadFileForm
          };
          param.check_time = Number(param.check_time) / 1000;
          this.$api["UploadPhysicalExaminationReport"](param)
            .then(res => {
              this.$showMsg("", 3);
              this.$refs.uploadFileForm.resetFields();
              this.files = [];
              this.$refs.uploadFiles.clearFile();
              this.$emit("getVal", false);
            })
            .catch(msg => {
              this.$showMsg("");
            });
        } else {
          return false;
        }
      });
    },

    //
    getFiles(data) {
      this.uploadFileForm.report_file_list = data;
    }
  }
};
</script>

<style lang="scss" scoped>
</style>